*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    
}
.header{
    position: relative;
    width: 100%;
    height: 750px;
    background: #0B4E86;
    background-repeat: no-repeat;
    background-size: contain;
    background-attachment: fixed;
}
nav{
    background:  linear-gradient(#0B2747, #134E84);
}
.navbar-toggler:focus{
    box-shadow: none;
}

.text-gold{
    color: #F1D9BF;
}

.waves{
    position: absolute;
    bottom:0;
}

.main,.proje,.table-section,.partners,.hizmitler,.prefooter{
    background:#F5F5F5;
}
.py-150{
    padding-top: 150px;
    padding-bottom: 150px;
}

.text-main{
    color: #0B4E86;
}

.bg-main{
    background: #0B4E86;
}
.arrow-down{
    position: relative;
    margin: 70px auto;
    padding:10px 0;
    width: 58px;
    height: 58px;
    background:#0B4E86 ;
}

.proje-item{
    position: relative;
    height: 250px;
    
}
.proje-item-1{
    background:url('/src/images/proje-items/Group\ 2883.jpg') no-repeat center;
    background-size: cover;
}

.proje-item-2{
    background:url('/src/images/proje-items/109.jpg') no-repeat center;
    background-size: cover;
}

.proje-item-3{
    background:url('/src/images/proje-items/ispartakulegece0002-large-jpg30032017093519.jpg') no-repeat center;
    background-size: cover;
}

.proje-item-4{
    background:url('/src/images/proje-items/Group\ 2885.jpg') no-repeat center;
    background-size: cover;
}

.proje-item-5{
    background:url('/src/images/proje-items/Group\ 2872.jpg') no-repeat center;
    background-size: cover;
}


.proje-content{
    position: absolute;
    width: 100%;
    left: 0;
    bottom: 0;
    background: linear-gradient( transparent,#0B2747);
    color: #fff;
    
}

.proje-icon{
    
    
}

.proje-icon-1{
    position: absolute;
    width: 112px;
    height: 79px;
    top: 30%;
    right: 40%;
}
.proje-icon-2{
    position: absolute;
    width: 137px;
    height: 137px;
    top: 20%;
    right: 30%;
}
.partner-col{
    position: relative;
    width: 150px;
    height: 150px;
    margin-bottom: 30px;
}
.partner-item{
    position: relative;
    width:75px;
    height: 75px;
}
.partner-badge{
    position: absolute;
    width: 100%;
    height: 100%;
    background: #0B4E86;
    transform: rotate(45deg);
    border-radius: 15px;
    z-index: 0;
}
.partner-icon{
    position: absolute;
    top:15%;
    right:25%;
    z-index: 1;
}

.text-black-50{
    color: #242424;
}

.fs-13{
    font-size: 13px;
}
.fs-10{
    font-size: 10px;
}

.btn-main {
    color: #fff;
    background-color: #0B4E86;
    border-color: #0B4E86;
}

.footer{
    background: #122E46;
}
.bg-main-2{
    background: #134E84;
}